import React from "react";
import ProjectGallery from "../components/ProjectGallery";

import { Title, MainWrapper } from "../components/Headings";
import { GalleryWrapper } from "../components/ProjectsStyles";

export default class Projects extends React.Component {
  constructor(props) {
    super(props);
    this.state = { images: this.props.images };
  }

  setCustomTags(i) {
    return i.tags.map(t => {
      return (
        <div key={t.value} style={customTagStyle}>
          {t.title}
        </div>
      );
    });
  }

  render() {
    var images = this.state.images.map(i => {
      i.customOverlay = (
        <div style={captionStyle}>
          <div>{i.caption}</div>
          {i.hasOwnProperty("tags") && this.setCustomTags(i)}
        </div>
      );
      return i;
    });

    return (
      <MainWrapper color="#e74c3c">
        <Title>Projects</Title>
        <p>
          I've worked on many types of projects both personal and professional.
          Here are some projects that are dear to my heart.
        </p>
        <GalleryWrapper>
          <ProjectGallery
            images={images}
            enableImageSelection={false}
            backdropClosesModal={true}
          />
        </GalleryWrapper>
      </MainWrapper>
    );
  }
}

const captionStyle = {
  backgroundColor: "rgba(0, 0, 0, 0.8)",
  maxHeight: "240px",
  overflow: "hidden",
  position: "absolute",
  bottom: "0",
  width: "100%",
  color: "white",
  padding: "2px",
  fontSize: "90%"
};

const customTagStyle = {
  wordWrap: "break-word",
  display: "inline-block",
  backgroundColor: "white",
  height: "auto",
  fontSize: "75%",
  fontWeight: "600",
  lineHeight: "1",
  padding: ".2em .6em .3em",
  borderRadius: ".25em",
  color: "black",
  verticalAlign: "baseline",
  margin: "2px"
};

Projects.defaultProps = {
  images: [
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        {
          value: "React",
          title: "Read more...",
          link: "/about"
        }
      ],
      caption: "First Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        {
          value: "Node",
          title: "Read more...",
          link: "/about"
        },
        { value: "Express", title: "Read more...", link: "/about" }
      ],
      caption: "Second Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        { value: "Gastby", title: "Read more...", link: "/about" },
        { value: "React", title: "Project Page", link: "/contact" }
      ],
      caption: "Third Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        { value: "Express", title: "Read more...", link: "/about" },
        { value: "AMP", title: "Project Page", link: "/contact" }
      ],
      caption: "Fourth Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        {
          value: "Bootstrap",
          title: "Read more...",
          link: "/about"
        }
      ],
      caption: "Fifth Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        { value: "Javascript", title: "Read more...", link: "/about" },
        { value: "Elasticsearch", title: "Project Page", link: "/contact" }
      ],
      caption: "Sixth Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        { value: "React v16", title: "Read more...", link: "/about" },
        { value: "React-Router v4", title: "Project Page", link: "/contact" }
      ],
      caption: "Seventh Project"
    },
    {
      src: "/images/project-images.png",
      thumbnail: "/images/project-thumbnails.png",
      thumbnailWidth: 350,
      thumbnailHeight: 180,
      tags: [
        {
          value: "Neo4j",
          title: "Read more...",
          link: "/about"
        }
      ],
      caption: "Eights"
    }
  ]
};



